/*Register progress bar*/
.Register-progress-bar{
    display: flex;
    width: 70%;
    margin-top: 7%;
    margin-left: 15%;
}

.Register-progress-bar .Register-step{
    text-align: center;
    width: 100%;
    position: relative;
}

.Register-progress-bar .Register-step .Register-bullet{
    position: relative;
    height: 25px;
    width: 25px;
    border: 2px solid silver;
    display: inline-block;
    border-radius: 50%;  
    transition: 0.8s; 
}

.Register-progress-bar .Register-step .Register-bullet.active{
    border-color: rgb(32, 32, 32);
    background: rgb(32, 32, 32);
}

.Register-progress-bar .Register-step:last-child .Register-bullet:before,
.Register-progress-bar .Register-step:last-child .Register-bullet:after{
    display: none;
}

.Register-progress-bar .Register-step .Register-bullet:before,
.Register-progress-bar .Register-step .Register-bullet:after{
    position: absolute;
    content: '';
    bottom: 11px;
    right: -60px;
    height: 3px;
    width: 58px;
    background: silver;
}

.Register-progress-bar .Register-step .Register-bullet.active:after{
    background: rgb(32, 32, 32);
    transform: scaleX(0);
    transform-origin: left;
    animation: animat .5s linear forwards ;
}
@keyframes animat{
    100%{
        transform: scaleX(1) ;
    }
}

.Register-progress-bar .Register-step .Register-bullet  p{
    font-size: 17px;
    color: silver;
    margin-top: 3px;
    transition: .5s;
}

.Register-progress-bar .Register-step .Register-bullet.active  p{
    display: none;
}

.Register-progress-bar .Register-step .Register-check{
    position: absolute;
    left: 50%;
    top: 60%;
    font-size: 15px;
    transform: translate(-50%, -50%);
    display: none;
}

.Register-progress-bar .Register-step .Register-check.active{
    display: block;
    color: #fff;
}
